*,
*:before,
*:after {
  box-sizing: border-box;
}
ul {
  margin: 0;
  padding: 0;
  list-style: none;
}
h4 {
  margin: .2em 0;
}
.SectionTitle {
  margin: 1em 0;
  padding-bottom: .2em;
  font-size: 1.5em;
  border-bottom: .1em solid;
}
.Btn {
  margin: 0;
  padding: .5em .7em;
  background: #C4C4C4;
  border: none;
  transition: all .3s;
}
.Btn:hover {
    cursor: pointer;
    opacity: .9;
    box-shadow: 0 0.2em 0.2em rgba(0, 0, 0, 0.4);
}
.Btn:disabled, .Btn:disabled:hover {
    cursor: default;
    opacity: 1;
    background: #E3E3E3;
    box-shadow: none;
    color: #999;
}
body {
  margin: 0;
  font-family: 'Fira Mono', monospace;
}
li {
  margin: 1em 0;
}
.Splash {
  padding-top: 3em;
  text-align: center;
}
.Footer {
  width: 100%;
  margin-top: 2em;
  padding: 1em;
  background: rgba(255, 255, 255, 0.8);
}
@media screen and (min-width: 680px) {
.Footer {
      position: fixed;
      bottom: 0;
      left: 0;
}
}

.Header {
  width: 100%;
  position: fixed;
  background: #DDD;
}
.Header__content {
    max-width: 1400px;
    margin: 0 auto;
    padding: 1em 1em 1em 4em;
}
@media screen and (min-width: 680px) {
.Header__content {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 0 1em;
}
}
@media screen and (min-width: 1400) {
.Header__content {
        padding: 0;
}
}
.Main {
  width: 95%;
  max-width: 1400px;
  margin: 0 auto;
  padding-top: 4.2em;
}
@media screen and (min-width: 680px) {
.Main {
      display: flex;
      justify-content: space-between;
      padding-bottom: 4em;
}
.Main > * {
        flex: 1 1 0;
}
}
@media screen and (min-width: 680px) {
.Main__column {
      padding: 0 1em;
}
}

.Menu {
  position: absolute;
  left: 1em;
  top: 50%;
  margin-top: -1.35em;
}
@media screen and (min-width: 680px) {
.Menu {
      display: none;
}
}
.Menu__toggle {
    width: 2.5em;
    height: 2.5em;
    padding: .7em .5em;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.Menu__toggle span, .Menu__toggle:before, .Menu__toggle:after {
      content: '';
      display: block;
      width: 100%;
      height: 1px;
      background: #000;
}
.Menu__dropdown {
    min-width: 50%;
    position: fixed;
    left: 0;
    top: 4em;
    padding: 0 1em;
    background: #DDD;
}
.Menu__dropdown li {
      margin: 0;
      padding: .8em 0;
      border-bottom: 1px solid #C4C4C4;
}
.Menu__dropdown li:last-child {
        border-bottom: none;
}

.Stats {
  display: flex;
  margin: 0;
  padding: 0;
  list-style: none;
}
.Stats__item {
    margin: 0;
    white-space: nowrap;
    padding: .1em 1em .1em 0;
}
@media screen and (min-width: 680px) {
.Stats__item {
        margin: 1em 0;
        padding-left: 1em;
}
}
.Stats__icon {
    filter: grayscale(50%);
}
.warning {
  color: #fa0;
}
.danger {
  color: #c00;
}








































.Modal {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 9;
}
.Modal:before {
    content: '';
    display: block;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background: rgba(0, 0, 0, 0.6);
}
.Modal__content {
    width: 40em;
    max-width: 95%;
    padding: 1em;
    position: absolute;
    top: 40%;
    left: 50%;
    transform: translate(-50%, -50%);
    background: #fff;
    text-align: center;
}

.Action {
  display: block;
  width: 100%;
  margin: 1em 0;
  padding: .7em;
  border: none;
  text-align: left;
}
.Action__title {
    margin: 0;
    font-size: 1.4em;
}
.Action__stats {
    margin: .2em 0;
    color: #666;
}
.Action__desc {
    margin: 0;
}
.Action:disabled .Action__stats {
    color: #bbb;
}
.progress:after {
  content: '...';
  animation: progress 1.5s linear infinite;
  display: inline;
}
@keyframes progress {
0% {
    content: '.';
}
33.333% {
    content: '..';
}
66.666% {
    content: '...';
}
}

.Item {
  margin: 0;
  padding: 0 0 .5em;
  border-bottom: .05em solid #333;
}
.Item p {
    margin: .5em 0;
}
.Item__actions {
    width: 100%;
    display: flex;
    justify-content: space-between;
}
.Item__actions--multi {
      flex-direction: row-reverse;
}

.GameStatusButton {
  margin: 0;
  padding: .2em;
  background: #eaeaea;
  border: none;
  vertical-align: middle;
  line-height: 0;
  cursor: pointer;
}
